window.addEventListener("popstate", function (e) {
  var state = e.state;

  switch (state.type) {
    case "layout_chooser":
      start_layout_gallery(state.data, false);
      break;

    case "layout_edit":
      start_layout_editor(state.data, false);
      break;

    default:
      console.error("How did you get to that state?");
  }
});